'use strict';

var _ = require('underscore'),
    Im = require('immutable'),
    invariant = require('react/lib/invariant');


  function PaginatedList(idsArray) {
    this.$PaginatedList_ids = Im.OrderedSet(idsArray) || Im.OrderedSet();
    this.$PaginatedList_pageCount = 0;
    this.$PaginatedList_nextPageUrl = '/';
    //this._isExpectingPage = false;
  }

  Object.defineProperty(PaginatedList.prototype,"getIds",{writable:true,configurable:true,value:function() {
    return this.$PaginatedList_ids;
  }});

  Object.defineProperty(PaginatedList.prototype,"getPageCount",{writable:true,configurable:true,value:function() {
    return this.$PaginatedList_pageCount;
  }});
  //
  //isExpectingPage() {
  //  return this._isExpectingPage;
  //}

  Object.defineProperty(PaginatedList.prototype,"getNextPageUrl",{writable:true,configurable:true,value:function() {
    return this.$PaginatedList_nextPageUrl;
  }});

  Object.defineProperty(PaginatedList.prototype,"isLastPage",{writable:true,configurable:true,value:function() {
    return this.getNextPageUrl() === null && this.getPageCount() > 0;
  }});

  Object.defineProperty(PaginatedList.prototype,"prepend",{writable:true,configurable:true,value:function(id) {
    this.$PaginatedList_ids = this.$PaginatedList_ids.toList().unshift(id).toOrderedSet();
  }});

  Object.defineProperty(PaginatedList.prototype,"push",{writable:true,configurable:true,value:function(id) {
    this.$PaginatedList_ids = this.$PaginatedList_ids.add(id);
  }});

  Object.defineProperty(PaginatedList.prototype,"remove",{writable:true,configurable:true,value:function(id) {
    this.$PaginatedList_ids = this.$PaginatedList_ids.delete(id);
  }});

  //expectPage() {
  //  invariant(!this._isExpectingPage, 'Cannot call expectPage twice without prior cancelPage or receivePage call.');
  //  this._isExpectingPage = true;
  //}
  //
  //cancelPage() {
  //  invariant(this._isExpectingPage, 'Cannot call cancelPage without prior expectPage call.');
  //  this._isExpectingPage = false;
  //}

  Object.defineProperty(PaginatedList.prototype,"receivePage",{writable:true,configurable:true,value:function(newIdsArray, nextPageUrl) {
    //invariant(this._isExpectingPage, 'Cannot call receivePage without prior expectPage call.');

    if (newIdsArray.length) {
      this.$PaginatedList_ids = this.$PaginatedList_ids.union(newIdsArray);
    }

    //this._isExpectingPage = false;
    this.$PaginatedList_nextPageUrl = nextPageUrl || null;
    this.$PaginatedList_pageCount++;
  }});


module.exports = PaginatedList;