'use strict';

var React = require('react'),
    $__0=    React,PropTypes=$__0.PropTypes,
    PureRenderMixin = require('react/addons').addons.PureRenderMixin,
    DocumentTitle = require('react-document-title'),
    PageActions = require('../../actions/commons/PageActions'),
    UIActions = require('../../actions/commons/UIActions');


var Empty = React.createClass({displayName: "Empty",

  mixins:[PureRenderMixin],

  propTypes: {
    params: PropTypes.object.isRequired,
    query: PropTypes.object.isRequired,
    account: PropTypes.object.isRequired,
    auth: PropTypes.object.isRequired
  },

  componentDidMount: function() {
    this._checkOverlay();
  },

  componentWillUnmount: function() {
    PageActions.resetReturnpage();
  },

  _checkOverlay: function() {
    var pathString = this.props.path.toLowerCase().trim();
    if (pathString === String('/login')) {
      UIActions.showOverlay('login');
    } else if (pathString === String('/register')) {
      UIActions.showOverlay('register');
    }
  },

  render: function() {
    return (
      /* jshint ignore:start */
      React.createElement(DocumentTitle, {title: "Login Page"}, 
        React.createElement("div", {className: "main-container"}
        )
      )
      /* jshint ignore:end */
    );
  }

});

module.exports = Empty;
