'use strict';

var React = require('react'),
    $__0=    React,PropTypes=$__0.PropTypes,
    Reflux = require('reflux'),
    PureRenderMixin = require('react/addons').addons.PureRenderMixin,
    Link = require('react-router').Link,
    // actions
    PostsActions = require('../../../../actions/posts/PostsActions'),
    // components
    CommentUpvote = require('./upvote/CommentUpvote.jsx'),
    //store
    PostStore = require('../../../../stores/posts/PostStore');

// This comment using PostStore for link to source post.
var Comment = React.createClass({displayName: "Comment",

  mixins: [
    PureRenderMixin,
    Reflux.listenTo(PostStore, 'onPostUpdate'),

    require('../../../mixins/pluralize'),
    require('../../../mixins/abbreviateNumber'),
    require('../../../mixins/hostNameFromUrl'),
    require('../../../mixins/timeAgo')
  ],

  propTypes: {
    comment: PropTypes.object.isRequired,
    auth: PropTypes.object.isRequired,
    showPostTitle: PropTypes.bool.isRequired
  },

  getDefaultProps: function() {
    return {
      showPostTitle: false
    };
  },

  getInitialState: function() {
    var post = PostStore.get(this.props.comment.post);
    return {
      post: post
    };
  },

  componentDidMount: function() {
    if(typeof this.state.post === 'undefined' &&
      typeof this.props.comment.post !== 'undefined') {
      PostsActions.getPost(this.props.comment.post);
    }
  },

  onPostUpdate: function() {
    var post = PostStore.get(this.props.comment.post);
    this.setState({
      post: post
    });
    if(typeof this.state.post === 'undefined' &&
        typeof this.props.comment.post !== 'undefined') {
      PostsActions.getPost(this.props.comment.post);
    }
  },

  render: function() {
    var comment = this.props.comment;
    var post = this.state.post;
    var auth = this.props.auth;
    var deleteOption = '';
    var showPostTitle = this.props.showPostTitle;

    if (comment.isDeleted) {
      // post doesn't exist
      return (
        /* jshint ignore:start */
        React.createElement("div", {className: "comment cf"}, 
          "[deleted]"
        )
        /* jshint ignore:end */
      );
    }

    // add delete option if creator is logged in
    if (comment.isAuthorMe) {
      deleteOption = (
        /* jshint ignore:start */
        React.createElement("span", {className: "delete post-info-item"}, 
          React.createElement("a", {onClick:  PostsActions.deleteComment.bind(this, comment.id) }, "delete")
        )
        /* jshint ignore:end */
      );
    }

    var postLink = null;
    if (showPostTitle && typeof post !== 'undefined') {
      postLink = (
        /* jshint ignore:start */
        React.createElement("span", {className: "post-info-item"}, 
          React.createElement(Link, {to: "singlePost", params: { postId: post.id}}, " ",  post.title, " ")
        )
        /* jshint ignore:end */
      );
    }

    return (
      /* jshint ignore:start */
      React.createElement("div", {className: "comment cf"}, 
        React.createElement("div", {className: "comment-text"}, 
           comment.content
        ), 
        React.createElement("div", {className: "comment-info"}, 
          React.createElement("div", {className: "posted-by float-left"}, 
            React.createElement(CommentUpvote, {comment:  comment, auth:  auth }), 
            React.createElement("span", {className: "post-info-item"}, 
              React.createElement(Link, {to: "user", params: { userId: comment.author.id}}, 
                 comment.author.username, " ")
            ), 
            React.createElement("span", {className: "post-info-item"}, 
                 this.timeAgo(comment.createdAt) 
            ), 
             postLink, 
             deleteOption 
          )
        )
      )
      /* jshint ignore:end */
    );
  }

});

module.exports = Comment;
